import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, PlanoContaReferencialInstituicao } from '../../../../models/dtos/mcg/planoContaReferencialInstituicao';

interface PlanoContaMCGReferencialInstituicaoEditViewProps extends EditViewProps<PlanoContaReferencialInstituicao> {}

const PlanoContaMCGReferencialInstituicaoEditView: React.FC<PlanoContaMCGReferencialInstituicaoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<PlanoContaReferencialInstituicao>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Instituição de Plano Referencial">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline
          id="codigo"
          label="Código"
          formProps={{ ...formProps, validation: { required: true } }}
          isPrependLabel={true}
          placeholder="Informe o código"
        />

        <InputGroupInline
          id="instituicao"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { required: true } }}
          label="Instituição"
          placeholder="Informe o nome da Instituição"
        />

        <InputGroupInline
          id="codigoECD"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { required: true } }}
          label="Código ECD"
          placeholder="Informe o Código ECD"
        />
      </EditForm>
    </WindowComponent>
  );
};

export default PlanoContaMCGReferencialInstituicaoEditView;
